import React from 'react';

import './Card.css';

interface CardProps {
	title: string;
}

export const Card: React.FC<CardProps> = (props) => {
	return (
		<div className={'card'}>
			<h3>{props.title}</h3>
			{props.children}
		</div>
	);
};

export const CardContainer = (props: any) => (
	<div className={'card-container'} {...props}></div>
);
