import React, { ReactNode } from 'react';
import { Card } from '../card/Card';
import { Todo } from './Todo';

interface TodoCardProps {
	title: string;
	items: string[];
}

export const TodoCard: React.FC<TodoCardProps> = (props) => {
	let items: ReactNode[] = [];
	const getItems = () => {
		if (props.items.length < 5)
			items = props.items.map((item) => <Todo key={item}>{item}</Todo>);
		else if (props.items.length >= 5) {
			items = props.items
				.slice(0, 3)
				.map((item) => <Todo key={item}>{item}</Todo>);
			items.push(
				<Todo key={'last one'}>{`+ ${
					props.items.length - 3
				} more items...`}</Todo>,
			);
		}
		return items;
	};

	return <Card title={props.title}>{getItems()}</Card>;
};
