import { Check, Clear, Remove } from '@material-ui/icons';
import React from 'react';
import './Gym.css';

interface GymProps {
	days: number[];
	attendance: number;
}

const Cell: React.FC = (props) => {
	return <div className={'gym-cell'}>{props.children}</div>;
};

const GymCheck = () => {
	return (
		<p className={'gym-tick'}>
			<Check />
		</p>
	);
};

const GymCross = () => {
	return (
		<p className={'gym-cross'}>
			<Clear />
		</p>
	);
};

const GymSkip = () => {
	return (
		<p className={'gym-skip'}>
			<Remove />
		</p>
	);
};

const GymDoneMarker: React.FC<{ done: number }> = (props) => {
	if (props.done === 0) return <GymCross />;
	if (props.done === 1) return <GymCheck />;
	if (props.done === 2) return <GymSkip />;
	else return null;
};

export const Gym: React.FC<GymProps> = (props) => {
	return (
		<div className={'gym'}>
			<div className={'gym-cells'}>
				<Cell>
					<p>Mon</p>
					<GymDoneMarker done={props.days[0]} />
				</Cell>
				<Cell>
					<p>Tue</p>
					<GymDoneMarker done={props.days[1]} />
				</Cell>
				<Cell>
					<p>Wed</p>
					<GymDoneMarker done={props.days[2]} />
				</Cell>
				<Cell>
					<p>Thu</p>
					<GymDoneMarker done={props.days[3]} />
				</Cell>
				<Cell>
					<p>Fri</p>
					<GymDoneMarker done={props.days[4]} />
				</Cell>
				<Cell>
					<p>Sat</p>
					<GymDoneMarker done={props.days[5]} />
				</Cell>
				<Cell>
					<p>Sun</p>
					<GymDoneMarker done={props.days[6]} />
				</Cell>
			</div>
			<p>{props.attendance}% this month.</p>
		</div>
	);
};
