import React, { useEffect, useState } from 'react';
import './theme.css';
import './App.css';
import { Milestone, MilestonesContainer } from './components/milestone/Milestone';
import { Board } from './components/board/Board';
import { Gym } from './components/gym/Gym';
import { Quote } from './components/quote/Quote';
import { monthGymMock } from './mocks/gymMock';
import { CountdownModel } from './models/CountdownModels';
import { MilestoneModel } from './models/MilestoneModels';
import { BoardModel } from './models/BoardModels';
import { GymWeekDaysModel } from './models/GymModels';
import CountdownServices from './services/CountdownServices';
import MilestoneServices from './services/MilestoneServices';
import BoardServices from './services/BoardServices';
import GymServices from './services/GymServices';
import { Countdowns } from './components/countdown/Countdowns';

const App: React.FC = () => {
	const [milestones, setMilestones] = useState<MilestoneModel[]>([]);
	const [boards, setBoards] = useState<BoardModel[]>([]);
	const [gymWeekDays, setGymWeekDays] = useState<GymWeekDaysModel>([]);
	const [countdowns, setCountdowns] = useState<CountdownModel[]>([]);

	useEffect(() => {
		MilestoneServices.getMilestones().then((res) => {
			setMilestones(res);
		});

		BoardServices.getBoards().then((res) => {
			setBoards(res);
		});

		GymServices.getGymWeekDays().then((res) => {
			setGymWeekDays(res);
		});

		CountdownServices.getCountdowns().then((res) => {
			setCountdowns(res);
		});
	}, []);

	return (
		<div className='App' data-theme='dark'>
			{boards.map((board) => {
				if (board.cards.length > 0)
					return (
						<Board
							key={board.boardId}
							title={board.title}
							cards={board.cards}
						/>
					);
				return null;
			})}
			<hr />
			<div>
				<h2>Milestones</h2>
				<MilestonesContainer>
					{milestones
						.sort((a, b) => a.due.localeCompare(b.due))
						.map((milestone) => (
							<Milestone key={milestone.milestoneId} milestone={milestone} />
						))}
				</MilestonesContainer>
			</div>
			<hr />
			<div>
				<h2>Gym</h2>
				<Gym days={gymWeekDays} attendance={monthGymMock} />
			</div>
			<hr />
			<Countdowns countdowns={countdowns} />
			<hr />
			<Quote />
		</div>
	);
};

export default App;
