import { gymMock } from '../mocks/gymMock';
import { GymWeekDaysModel } from '../models/GymModels';
import { ApiEndpoint, ApiMethod, ApiUrl, environment, Mode } from './constants';

const GymServices = {
	getGymWeekDays(): Promise<GymWeekDaysModel> {
		if (environment === Mode.dev) {
			return Promise.resolve(gymMock);
		}
		const url = `${ApiUrl}${ApiEndpoint.GYM}`;
		const requestInit: RequestInit = {
			method: ApiMethod.GET,
		};
		return fetch(url, requestInit).then((res) => res.json());
	},
};

export default GymServices;
