import { boardMock } from '../mocks/boardsMock';
import { BoardModel } from '../models/BoardModels';
import { ApiEndpoint, ApiMethod, ApiUrl, environment, Mode } from './constants';

const BoardServices = {
	getBoards(): Promise<BoardModel[]> {
		if (environment === Mode.dev) {
			return Promise.resolve(boardMock);
		}
		const url = `${ApiUrl}${ApiEndpoint.BOARDS}`;
		const requestInit: RequestInit = {
			method: ApiMethod.GET,
		};
		return fetch(url, requestInit).then((res) => res.json());
	},
};

export default BoardServices;
